import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  Card,
  CardContent,
  TextField,
  Button,
  Box,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@mui/material";
import { GetOneUser, UpdateUser } from "../../services/users-service";
import { signUp } from "../../services/auth-service";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";

const Form = () => {
  const [user, setUser] = useState({
    name: "",
    lastName: "",
    email: "",
    password: "",
    role: "",
    photo: "",
    createdAt: {
      date: new Date().toLocaleDateString(),
      time: new Date().toLocaleTimeString(),
    },
  });

  const [btnInfo, setBtnInfo] = useState("");

  const handleInputChange = ({ target }) => {
    setUser({
      ...user,
      [target.name]: target.value,
    });
  };

  const navigate = useNavigate();

  useEffect(() => {
    let id = localStorage.getItem("id");
    if (id) {
      GetOneUser(id)
        .then((json) => {
          setUser(json.data);
          setBtnInfo("Guardar Cambios");
        })
        .catch((err) => console.log(err));
    } else {
      setBtnInfo("Agregar");
    }
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    let id = localStorage.getItem("id");
    if (!id) {
      signUp(user)
        .then((json) => {})
        .catch((err) => console.log(err));
      navigate(-1);
    } else {
      UpdateUser(user, id)
        .then((json) => {})
        .catch((err) => console.log(err));
      navigate(-1);
    }
  };
  return (
    <Box
      sx={{
        display: "flex",
        justifyContent: "center",
        mt: 10,
        flexDirection: "column",
        position: "relative",
      }}
    >
      <Box sx={{ position: "absolute", top: "-2%", left: "1%" }}>
        <ArrowBackIcon sx={{ fontSize: 55 }} onClick={() => navigate(-1)} />
      </Box>
      <Typography variant="h2" textAlign="center" sx={{ mb: 1 }}>
        {localStorage.getItem("id") ? "Editar Usuario" : "Agregar Usuario"}
      </Typography>

      <Box sx={{ mt: 3, display: 'flex', justifyContent: 'center' }}>
        <form onSubmit={handleSubmit}>
          <Card sx={{ width: 400 }}>
            <CardContent
              sx={{ display: "flex", flexDirection: "column", gap: 1 }}
            >
              <TextField
                id="outlined-multiline-flexible"
                label="Nombre"
                fullWidth
                type="text"
                name="name"
                onChange={handleInputChange}
                value={user.name}
              />
              <TextField
                id="outlined-multiline-flexible"
                label="Apellido"
                fullWidth
                type="text"
                name="lastName"
                onChange={handleInputChange}
                value={user.lastName}
              />
              <TextField
                id="filled-multiline-static"
                label="Email"
                fullWidth
                type="email"
                name="email"
                onChange={handleInputChange}
                value={user.email}
              />
              <TextField
                id="outlined-multiline-flexible"
                label="Contraseña"
                fullWidth
                type="password"
                name="password"
                onChange={handleInputChange}
                value={user.password}
              />
              <FormControl>
                <InputLabel id="role">Categoria</InputLabel>
                <Select
                  label="Categoria"
                  id="role"
                  name="role"
                  value={user.role}
                  onChange={handleInputChange}
                >
                  <MenuItem value="admin">Admin</MenuItem>
                  <MenuItem value="client">Client</MenuItem>
                </Select>
              </FormControl>
              {/* <TextField
                name="cover"
                type="file"
                fullWidth
                onChange={handleFileChange}
              /> */}
              <Button variant="contained" color="success" type="submit">
                {btnInfo}
              </Button>
            </CardContent>
          </Card>
        </form>
      </Box>
    </Box>
  );
};

export default Form;
