import {
  Button,
  CssBaseline,
  TextField,
  Grid,
  Box,
  Typography,
  Container,
  Paper,
} from "@mui/material";
import { useState } from "react";
import { signUp } from "../../services/auth-service";
import { useNavigate } from "react-router-dom";
import { AlertInfo } from "../utils/Alerts";
import { fileUpload } from "../helpers/fileUpload";
import WbSunnyIcon from "@mui/icons-material/WbSunny";
import DarkModeIcon from "@mui/icons-material/DarkMode";
import { changeTheme } from "../../redux/themeSlice";
import { useDispatch, useSelector } from "react-redux";
import { ValidateHook } from "../hooks/ValidationHook";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import ErrorIcon from "@mui/icons-material/Error";

const Register = () => {
  const initialState = {
    name: "",
    lastName: "",
    email: "",
    password: "",
    photo: "",
    role: "client",
    createdAt: {
      date: new Date().toLocaleDateString(),
      time: new Date().toLocaleTimeString(),
    },
  };
  const [user, validate, handleInputChange, userValid, setUserValid] =
    ValidateHook(initialState);
  const [passwordTouched, setPasswordTouched] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [alertColor, setAlertColor] = useState("");
  const [alertContent, setAlertContent] = useState("");
  const themeState = useSelector((store) => store.theme);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      signUp(user).then((json) => {
        if (json.token) {
          navigate("/");
          setAlertColor("success");
        }
        setAlertColor("error");
        setShowAlert(true);
        setAlertContent(json.message);
      });
      setTimeout(() => setShowAlert(false), 4000);
    } catch (error) {
      console.log(error);
    }
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    fileUpload(file)
      .then((resp) => {
        user.photo = resp;
        setUserValid(true);
      })
      .catch((error) => {
        console.warn(error);
      });
  };

  const PasswordValidator = () => {
    const HasCapital = () => {
      return validate.password.hasCapital ? (
        <Box sx={{ display: "flex", gap: 2 }}>
          <CheckCircleIcon color="success" />
          <Typography component="subtitle" sx={{ color: "#66bb6a" }}>
            Una letra mayuscula
          </Typography>
        </Box>
      ) : (
        <Box sx={{ display: "flex", gap: 2 }}>
          <ErrorIcon color="error" />
          <Typography component="subtitle" sx={{ color: "#f44336" }}>
            Una letra mayuscula
          </Typography>
        </Box>
      );
    };
    const HasNumber = () => {
      return validate.password.hasNumber ? (
        <Box sx={{ display: "flex", gap: 2 }}>
          <CheckCircleIcon color="success" />
          <Typography component="subtitle" sx={{ color: "#66bb6a" }}>
            Un numero
          </Typography>
        </Box>
      ) : (
        <Box sx={{ display: "flex", gap: 2 }}>
          <ErrorIcon color="error" />
          <Typography component="subtitle" sx={{ color: "#f44336" }}>
            Un numero
          </Typography>
        </Box>
      );
    };
    const IsMin8 = () => {
      return validate.password.isMin8 ? (
        <Box sx={{ display: "flex", gap: 2 }}>
          <CheckCircleIcon color="success" />
          <Typography component="subtitle" sx={{ color: "#66bb6a" }}>
            Minimo 8 caracteres
          </Typography>
        </Box>
      ) : (
        <Box sx={{ display: "flex", gap: 2 }}>
          <ErrorIcon color="error" />
          <Typography component="subtitle" sx={{ color: "#f44336" }}>
            Minimo 8 caracteres
          </Typography>
        </Box>
      );
    };
    return passwordTouched ? (
      <Box sx={{ mt: 3, display: "flex", flexDirection: "column" }}>
        <HasCapital />
        <HasNumber />
        <IsMin8 />
      </Box>
    ) : (
      ""
    );
  };

  return (
    <>
      <AlertInfo
        show={showAlert}
        severity={alertColor}
        content={alertContent}
      />
      <Box
        sx={{ display: "flex", justifyContent: "center", mt: 3 }}
        onClick={() => dispatch(changeTheme())}
      >
        {themeState.isDark ? (
          <WbSunnyIcon sx={{ fontSize: 60 }} />
        ) : (
          <DarkModeIcon sx={{ fontSize: 60 }} />
        )}
      </Box>
      <Container component="main" sx={{ width: "420px", minWidth: "380px" }}>
        <CssBaseline />
        <Paper
          sx={{
            marginTop: 5,
            p: 2,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Typography component="h1" variant="h4">
            Registrarse
          </Typography>
          <Box
            component="form"
            noValidate
            onSubmit={handleSubmit}
            sx={{ mt: 3 }}
          >
            <Grid
              sx={{ display: "flex", justifyContent: "center" }}
              container
              spacing={3}
            >
              <Grid item xs={8}>
                <TextField
                  autoComplete="given-name"
                  name="name"
                  error={
                    validate.name.isValid === null
                      ? false
                      : !validate.name.isValid
                      ? true
                      : false
                  }
                  helperText={
                    !validate.name.isValid ? validate.name.message : ""
                  }
                  required
                  fullWidth
                  value={user.name}
                  onChange={handleInputChange}
                  id="name"
                  label="Nombre"
                  autoFocus
                />
              </Grid>
              <Grid item xs={8}>
                <TextField
                  required
                  fullWidth
                  value={user.lastName}
                  onChange={handleInputChange}
                  error={
                    validate.lastName.isValid === null
                      ? false
                      : !validate.lastName.isValid
                      ? true
                      : false
                  }
                  helperText={
                    !validate.lastName.isValid ? validate.lastName.message : ""
                  }
                  id="lastName"
                  label="Apellido"
                  name="lastName"
                  autoComplete="family-name"
                />
              </Grid>
              <Grid item xs={8}>
                <TextField
                  required
                  name="email"
                  fullWidth
                  value={user.email}
                  onChange={handleInputChange}
                  error={
                    validate.email.isValid === null
                      ? false
                      : !validate.email.isValid
                      ? true
                      : false
                  }
                  helperText={
                    !validate.email.isValid ? validate.email.message : ""
                  }
                  id="email"
                  label="Email"
                  autoComplete="email"
                />
              </Grid>
              <Grid item xs={8}>
                <TextField
                  required
                  fullWidth
                  name="password"
                  value={user.password}
                  onChange={(e) => {
                    handleInputChange(e);
                    setPasswordTouched(true);
                  }}
                  error={
                    validate.password.isValid === null
                      ? false
                      : !validate.password.isValid
                      ? true
                      : false
                  }
                  label="Contraseña"
                  type="password"
                  id="password"
                  autoComplete="new-password"
                />
              </Grid>
              <PasswordValidator/>
              <Grid item xs={8}>
                <TextField
                  name="cover"
                  type="file"
                  fullWidth
                  onChange={handleFileChange}
                />
              </Grid>
            </Grid>
            <Box sx={{ display: "flex", justifyContent: "center", mt: 2 }}>
              <Button
                type="submit"
                variant="contained"
                disabled={!userValid}
                sx={{ width: "50%" }}
              >
                Registrarse
              </Button>
            </Box>
          </Box>
        </Paper>
      </Container>
    </>
  );
};

export default Register;
