import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getAllAsync, getByName } from "../../redux/async-redux";
import {
  Box,
  Card,
  Button,
  CardContent,
  Typography,
  TextField,
  InputLabel,
  Select,
  MenuItem,
  FormControl,
} from "@mui/material";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import { addItem } from "../../redux/shopCartSlice";
import { AlertDialog, AlertSnack } from "../utils/Alerts";
import InfoIcon from "@mui/icons-material/Info";
import { setItems } from "../../redux/shopCartSlice";
import { getLS } from "../helpers/localStorage";
import { sortProducts } from "../../redux/productSlice";

const Main = () => {
  const dispatch = useDispatch();
  const productState = useSelector((state) => state.products);
  const shopCartState = useSelector((state) => state.shopCart);
  const [openDialog, setOpenDialog] = useState(false);
  const [openSuccess, setOpenSuccess] = useState(false);
  const [productName, setProductName] = useState("");
  const [filter, setFilter] = useState("Todas");
  const [sort, setSort] = useState("");

  const handleFilterChange = (event) => {
    setFilter(event.target.value);
  };

  const handleSortChange = (event) => {
    setSort(event.target.value);
    dispatch(
      sortProducts({ type: event.target.value, array: productState.products })
    );
  };

  const ProductCard = ({ product }) => {
    return (
      <Card
        sx={{
          width: 320,
          maxWidth: "sm",
          p: 2,
        }}
      >
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
          }}
        >
          <Box sx={{ m: "auto" }}>
            <img src={product.photo} alt="Cover" width="200px" />
          </Box>
          <CardContent sx={{ display: "flex", flexDirection: "column" }}>
            <Typography variant="h5" textAlign="center">
              {product.name}
            </Typography>
            <Box
              sx={{
                display: "flex",
                flexDirection: "column",
                mt: 2,
                gap: 3,
              }}
            >
              <Typography sx={{ fontSize: 21 }} textAlign="center">
                Precio: {product.price}
              </Typography>
              <Typography sx={{ fontSize: 21 }} textAlign="center">
                Categoria: {product.category}
              </Typography>
            </Box>
            <Box
              sx={{
                display: "flex",
                gap: 3,
                p: 2,
              }}
            >
              <Button
                variant="contained"
                color="success"
                endIcon={<ShoppingCartIcon />}
                onClick={() => handleAddItem(product)}
              >
                Agregar
              </Button>
              <Button
                color="primary"
                variant="contained"
                endIcon={<InfoIcon />}
              >
                Detalle
              </Button>
            </Box>
          </CardContent>
        </Box>
      </Card>
    );
  };

  const CategoryType = () => {
    return filter !== "Todas"
      ? productState.products
          .filter((product) => product.category === filter)
          .map((product, index) => (
            <ProductCard product={product} key={index} />
          ))
      : productState.products.map((product, index) => (
          <ProductCard product={product} key={index} />
        ));
  };

  const handleSearch = ({ target }) => {
    if (target.value !== "") {
      dispatch(getByName(target.value));
    } else {
      dispatch(getAllAsync());
    }
  };

  const category = ["Todas", "Videojuegos", "Ropa", "Accesorios", "Celulares"];

  const sortType = [
    "Precio mayor a menor",
    "Precio menor a mayor",
    "Alfabeticamente [A-Z]",
    "Alfabeticamente [Z-A]",
  ];

  const handleSucessClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenSuccess(false);
  };

  useEffect(() => {
    dispatch(getAllAsync());
    dispatch(setItems(getLS()));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const checkItemInCart = (item) => {
    let itemFind = shopCartState.shopCart.find(
      (cartItem) => cartItem._id === item._id
    );
    if (itemFind) {
      setOpenDialog(!openDialog);
      setProductName(item.name);
    } else {
      setOpenSuccess(!openSuccess);
    }
  };

  const handleAddItem = (item) => {
    dispatch(addItem(item));
    checkItemInCart(item);
  };

  return (
    <>
      <Box sx={{ mt: 7.8 }}>
        <AlertDialog
          open={openDialog}
          productName={productName}
          handleClose={setOpenDialog}
        />
        <AlertSnack
          open={openSuccess}
          alertContent="Producto Agregado al carrito!"
          handleClose={handleSucessClose}
        />
      </Box>
      <Box sx={{ display: "flex", flexDirection: "column", mt: 5 }}>
        <Box
          sx={{
            display: "flex",
            justifyContent: "center",
            gap: 2,
            flexWrap: "wrap",
          }}
        >
          <Typography sx={{ fontSize: 65, fontWeight: 300 }} textAlign="center">
            Buscar
          </Typography>
          <Box
            sx={{
              display: "flex",
              justifyContent: "center",
              gap: 1,
              flexWrap: "wrap",
            }}
          >
            <FormControl>
              <TextField
                label="Escribe tu busqueda"
                id="search"
                size="large"
                fullWidth
                onChange={handleSearch}
                sx={{ mt: 3 }}
              />
            </FormControl>

            <Box sx={{ minWidth: 120, mt: 3 }}>
              <FormControl fullWidth>
                <InputLabel id="category">Categoria</InputLabel>
                <Select
                  labelId="category"
                  id="category"
                  value={filter}
                  fullWidth
                  label="Categoria"
                  onChange={handleFilterChange}
                >
                  {category.map((item, index) => (
                    <MenuItem key={index} value={item}>
                      {item}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Box>

            <Box sx={{ minWidth: 120, mt: 3 }}>
              <FormControl fullWidth>
                <InputLabel id="sort">Ordenar</InputLabel>
                <Select
                  labelId="sort"
                  id="sort"
                  value={sort}
                  label="Ordenar"
                  onChange={handleSortChange}
                >
                  {sortType.map((item, index) => (
                    <MenuItem key={index} value={item}>
                      {item}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Box>
          </Box>
        </Box>
        <Box
          sx={{
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "center",
            gap: 3,
            m: 2,
          }}
        >
          {/* Products List */}
          <CategoryType />
        </Box>
      </Box>
    </>
  );
};

export default Main;
