import {
  Box,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TableContainer,
  Paper,
  Typography,
  IconButton,
  Button,
  FormControl,
  TextField,
  InputLabel,
  MenuItem,
  Select,
} from "@mui/material";
import Swal from "sweetalert2";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { getAllAsync, getByName } from "../../redux/async-redux";
import { DialogConfirm } from "../utils/Alerts";
import { deleteProduct } from "../../services/products-service";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import { sortProducts } from "../../redux/productSlice";

const ProductList = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const state = useSelector((state) => state.products);
  const [openConfirm, setOpenConfirm] = useState(false);
  const [idDelete, setIdDelete] = useState(null);
  const [filter, setFilter] = useState("Todas");
  const [sort, setSort] = useState("");

  const handleFilterChange = (event) => {
    setFilter(event.target.value);
  };

  const handleSearch = ({ target }) => {
    if (target.value !== "") {
      dispatch(getByName(target.value));
      // setIsLoading(false);
    } else {
      dispatch(getAllAsync());
    }
  };

  const handleSortChange = (event) => {
    setSort(event.target.value);
    dispatch(sortProducts({ type: event.target.value, array: state.products }));
  };

  const handleCloseConfirm = () => {
    setOpenConfirm(false);
  };

  const handleOpen = () => setOpenConfirm(true);

  const category = ["Todas", "Videojuegos", "Ropa", "Accesorios", "Celulares"];

  const sortType = [
    "Precio mayor a menor",
    "Precio menor a mayor",
    "Alfabeticamente [A-Z]",
    "Alfabeticamente [Z-A]",
  ];

  useEffect(() => {
    dispatch(getAllAsync());
    const id = localStorage.getItem("id");
    if (id) {
      localStorage.removeItem("id");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const tableHeads = ["Imagen", "Nombre", "Precio", "Categoria", "Accion"];
  const handleDeleteProduct = () => {
    deleteProduct(idDelete)
      .then(() => {
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Producto Eliminado!',
          showConfirmButton: false,
          timer: 1500
        })
      })
      .catch((err) => console.log(err));
    handleCloseConfirm();
  };

  const ProductRow = ({ product }) => {
    return (
      <TableRow>
        <TableCell align="center">
          <img width="150px" src={product.photo} alt={product.name} />
        </TableCell>
        <TableCell align="center">
          <Typography variant="h5">{product.name}</Typography>
        </TableCell>
        <TableCell align="center">
          <Typography variant="h5">{product.price}</Typography>
        </TableCell>
        <TableCell align="center">
          <Typography variant="h5">{product.category}</Typography>
        </TableCell>
        <TableCell>
          <IconButton color="primary" aria-label="delete">
            <Link to="/admin/products/add" className="linkFix">
              <EditIcon
                color="secondary"
                sx={{ fontSize: 35 }}
                onClick={() => {
                  localStorage.setItem("id", product._id);
                }}
              />
            </Link>
          </IconButton>
          <IconButton color="error" aria-label="delete">
            <DeleteIcon
              sx={{ fontSize: 35 }}
              onClick={() => {
                setIdDelete(product._id);
                handleOpen();
              }}
            />
          </IconButton>
        </TableCell>
      </TableRow>
    );
  };

  const FilterTable = () => {
    return filter !== "Todas" ? (
      <TableBody>
        {state.products
          .filter((product) => product.category === filter)
          .map((product, index) => (
            <ProductRow product={product} key={index} />
          ))}
      </TableBody>
    ) : (
      <TableBody>
        {state.products.map((product, index) => (
          <ProductRow product={product} key={index} />
        ))}
      </TableBody>
    );
  };

  return (
    <Box sx={{ mt: 10 }}>
      <Box sx={{ display: "flex", justifyContent: "space-evenly" }}>
        {/* Go Back Button */}
        <ArrowBackIcon
          sx={{ fontSize: 55, mt: 1 }}
          onClick={() => navigate(-1)}
        />
        {/* Buscador */}
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            mb: 3,
            flexWrap: "wrap",
          }}
        >
          <Typography sx={{ fontSize: 65, fontWeight: 300 }} textAlign="center">
            Filtrar
          </Typography>
          <Box
            sx={{
              display: "flex",
              justifyContent: "center",
              gap: 1,
              flexWrap: "wrap",
            }}
          >
            <FormControl>
              <TextField
                label="Buscar"
                id="search"
                size="large"
                fullWidth
                onChange={handleSearch}
              />
            </FormControl>

            <Box sx={{ minWidth: 120 }}>
              <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">Categoria</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={filter}
                  fullWidth
                  label="Categoria"
                  onChange={handleFilterChange}
                >
                  {category.map((item, index) => (
                    <MenuItem key={index} value={item}>
                      {item}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Box>

            <Box sx={{ minWidth: 120 }}>
              <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">Ordenar</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={sort}
                  label="Ordenar"
                  onChange={handleSortChange}
                >
                  {sortType.map((item, index) => (
                    <MenuItem key={index} value={item}>
                      {item}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Box>
          </Box>
        </Box>
        {/* Add Button */}
        <Box sx={{ mt: 13 }}>
          <Link to="/admin/products/add" className="linkFix">
            <Button
              color="success"
              variant="contained"
              endIcon={<AddCircleIcon />}
            >
              Agregar Producto
            </Button>
          </Link>
        </Box>
      </Box>
      {/* Product Table */}
      <Box sx={{ display: "flex", justifyContent: "center" }}>
        <TableContainer component={Paper} sx={{ width: "85%" }}>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                {tableHeads.map((head, index) => (
                  <TableCell align="center" key={index}>
                    <Typography variant="h4">{head}</Typography>
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            {/* DIV */}
            <FilterTable/>
          </Table>
        </TableContainer>
        <DialogConfirm
          open={openConfirm}
          handleClose={handleCloseConfirm}
          title="¿Quieres borrar este producto?"
          action={() => handleDeleteProduct(idDelete)}
        />
      </Box>
    </Box>
  );
};

export default ProductList;
