import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import {
  Snackbar,
  Alert,
  Box,
  Card,
  CardContent,
  TextField,
} from "@mui/material";

export const AlertDialog = ({ open, productName, handleClose }) => {
  return open ? (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title" textAlign="center">
        {"El producto ya esta en el carrito"}
      </DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          Ya has agregado el producto "{productName}" al carrito.
        </DialogContentText>
      </DialogContent>
      <DialogActions sx={{ display: "flex", justifyContent: "center" }}>
        <Button
          variant="contained"
          color="error"
          onClick={() => handleClose(!open)}
        >
          Aceptar
        </Button>
      </DialogActions>
    </Dialog>
  ) : (
    ""
  );
};

export const AlertSnack = ({ open, handleClose, alertContent }) => {
  return (
    <Snackbar open={open} autoHideDuration={2000} onClose={handleClose}>
      <Alert onClose={handleClose} severity="success" sx={{ width: "250px" }}>
        {alertContent}
      </Alert>
    </Snackbar>
  );
};

export const AlertInfo = ({ show, severity, content }) => {
  return (
    <Box>{show ? <Alert severity={severity}> {content} </Alert> : ""}</Box>
  );
};

export const DialogConfirm = ({ open, handleClose, title, action }) => {
  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">{title}</DialogTitle>
      <DialogActions sx={{ display: "flex", justifyContent: "space-evenly" }}>
        <Button color="error" variant="contained" onClick={handleClose}>
          Cancelar
        </Button>
        <Button color="success" variant="contained" onClick={action}>
          Aceptar
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export const UserDialog = ({
  open,
  user,
  handleClose,
  handleChange,
  handleSubmit,
}) => {
  return open ? (
    <Dialog onClose={handleClose} open={open}>
      <DialogTitle textAlign="center">Editar Informacion</DialogTitle>
      <DialogContent>
        <form onSubmit={handleSubmit}>
          <Card sx={{ width: 400 }}>
            <CardContent
              sx={{ display: "flex", flexDirection: "column", gap: 2 }}
            >
              <TextField
                id="outlined-multiline-flexible"
                label="Nombre"
                fullWidth
                type="text"
                name="name"
                onChange={handleChange}
                value={user.name}
              />
              <TextField
                id="outlined-multiline-flexible"
                label="Apellido"
                fullWidth
                type="text"
                name="lastName"
                onChange={handleChange}
                value={user.lastName}
              />
              <TextField
                id="filled-multiline-static"
                label="Email"
                fullWidth
                type="email"
                name="email"
                onChange={handleChange}
                value={user.email}
              />
              <Button variant="contained" color="success" type="submit">
                Guardar Cambios
              </Button>
            </CardContent>
          </Card>
        </form>
      </DialogContent>
    </Dialog>
  ) : (
    ""
  );
};
