const url = "https://ecomerce-sebastian.herokuapp.com";

export const signUp = async (user) => {
	try {
	    const resp = await fetch(`${url}/users/createUser`, {
	        method: "POST",
	        headers: {
	            "Content-Type": "application/json",
	        },
	        body: JSON.stringify(user),
	    });
	    const json = await resp.json();
	    if(json.token){
	    	localStorage.setItem('token', json.token);
	    };
	    return json
	} catch(error){
		console.log(error);
	}
};  

export const signIn = async (user) => {
	try {
		const resp = await fetch(`${url}/users/loginUser`, {
	        method: "POST",
	        headers: {
	            "Content-Type": "application/json",
	        },
	        body: JSON.stringify(user),
	    });
	    const json = await resp.json();
		if(json.token){
	    	localStorage.setItem('token', json.token);
	    };
	    return json
	} catch(error) {
		console.log(error);
	}
};

export const Logout = () => {
	localStorage.removeItem('token');
};