const url = "https://ecomerce-sebastian.herokuapp.com";

export const getAllDB = async () => {
  try {
    let token = localStorage.getItem("token");
    let resp = await fetch(`${url}/products/getAll`, {
      method: "GET",
      headers: { Authorization: `Bearer ${token}` },
    });
    let json = await resp.json();
    return json;
  } catch (error) {
    console.log(error);
  }
};

export const getOne = async (id) => {
  try {
    let token = localStorage.getItem("token");
    let resp = await fetch(`${url}/products/getOne/${id}`, {
      method: "GET",
      headers: { Authorization: `Bearer ${token}` },
    });
    let json = await resp.json();
    return json;
  } catch (error) {
    console.log(error);
  }
};

export const createProduct = async (game) => {
  try {
    let token = localStorage.getItem("token");
    let resp = await fetch(`${url}/products/createProduct`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(game),
    });
    let json = await resp.json();
    return json;
  } catch (error) {
    console.log(error);
  }
};

export const searchProduct = async (name) => {
  try {
    const token = localStorage.getItem("token");
    const resp = await fetch(`${url}/products/searchProduct/${name}`, {
      method: "GET",
      headers: { Authorization: `Bearer ${token}` },
    });
    const json = await resp.json(resp);
    return json;
  } catch (error) {
    console.log(error);
  }
};

