import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  Card,
  CardContent,
  TextField,
  Button,
  Box,
  Typography,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
} from "@mui/material";
import { fileUpload } from "../helpers/fileUpload";
import {
  createProduct,
  getOne,
  updateProduct,
} from "../../services/products-service";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import Swal from "sweetalert2";

const Form = () => {
  const [product, setProduct] = useState({
    name: "",
    price: "",
    description: "",
    category: "",
    photo: "",
  });

  const categories = ["Videojuegos", "Ropa", "Accesorios", "Celulares"];

  const [isLoading, setIsLoading] = useState(false);

  const handleFileChange = (e) => {
    setIsLoading(true);
    setBtnInfo("Subiendo imagen...");
    const file = e.target.files[0];
    fileUpload(file)
      .then((resp) => {
        product.photo = resp;
        setIsLoading(false);
        setBtnInfo("Agregar");
      })
      .catch((error) => {
        console.warn(error);
      });
  };

  const [btnInfo, setBtnInfo] = useState("");

  const handleInputChange = ({ target }) => {
    setProduct({
      ...product,
      [target.name]: target.value,
    });
  };

  const navigate = useNavigate();

  useEffect(() => {
    let id = localStorage.getItem("id");
    if (id) {
      getOne(id)
        .then((json) => {
          setProduct(json.data);
          setBtnInfo("Guardar Cambios");
        })
        .catch((err) => console.log(err));
    } else {
      setBtnInfo("Agregar");
    }
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    let id = localStorage.getItem("id");
    if (!id) {
      createProduct(product)
        .then(() => {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Producto Creado!',
            showConfirmButton: false,
            timer: 1500
          })
        })
        .catch((err) => console.log(err));
      navigate(-1);
    } else {
      updateProduct(product, id)
        .then(() => {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Producto Editado!',
            showConfirmButton: false,
            timer: 1500
          })
        })
        .catch((err) => console.log(err));
      navigate(-1);
    }
  };
  return (
    <Box
      sx={{
        display: "flex",
        justifyContent: "center",
        mt: 10,
        flexDirection: "column",
        position: "relative",
      }}
    >
      <Box sx={{ position: "absolute", bottom: "90%", left: "1%" }}>
        <ArrowBackIcon sx={{ fontSize: 55 }} onClick={() => navigate(-1)} />
      </Box>
      <Typography variant="h2" textAlign="center" sx={{ mb: 1 }}>
        {localStorage.getItem("id") ? "Editar Producto" : "Agregar Producto"}
      </Typography>

      <Box sx={{ mt: 3, display: 'flex', justifyContent: 'center' }}>
        <form onSubmit={handleSubmit}>
          <Card sx={{ width: 400 }}>
            <CardContent
              sx={{ display: "flex", flexDirection: "column", gap: 1 }}
            >
              <TextField
                id="outlined-multiline-flexible"
                label="Nombre"
                fullWidth
                type="text"
                name="name"
                onChange={handleInputChange}
                value={product.name}
              />
              <TextField
                id="outlined-multiline-flexible"
                label="Precio"
                fullWidth
                multiline
                type="number"
                name="price"
                onChange={handleInputChange}
                value={product.price}
              />
              <TextField
                id="filled-multiline-static"
                label="Descripcion"
                multiline
                rows={4}
                fullWidth
                type="text"
                name="description"
                variant="filled"
                onChange={handleInputChange}
                value={product.description}
              />
              <FormControl>
                <InputLabel id="category">Categoria</InputLabel>
                <Select
                  label="Categoria"
                  id="category"
                  name="category"
                  value={product.category}
                  onChange={handleInputChange}
                >
                  {categories.map((category, index) => (
                    <MenuItem key={index} value={category}>
                      {category}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <TextField
                name="cover"
                type="file"
                fullWidth
                onChange={handleFileChange}
              />
              <Button
                variant="contained"
                color="success"
                type="submit"
                disabled={isLoading}
              >
                {btnInfo}
              </Button>
            </CardContent>
          </Card>
        </form>
      </Box>
    </Box>
  );
};

export default Form;
