const url = "https://ecomerce-sebastian.herokuapp.com";

export const GetAllUsers = async () => {
  try {
    const token = localStorage.getItem("token");
    const resp = await fetch(`${url}/getAll`, {
      method: "GET",
      headers: { Authorization: `Bearer ${token}` },
    });
    const json = await resp.json();
    return json;
  } catch (error) {
    console.log(error);
  }
};

export const GetOneUser = async (id) => {
  try {
    const token = localStorage.getItem("token");
    const resp = await fetch(`${url}/getOne/${id}`, {
      method: "GET",
      headers: { Authorization: `Bearer ${token}` },
    });
    const json = await resp.json();
    return json;
  } catch (error) {
    console.log(error);
  }
};

export const UpdateUser = async (user, id) => {
  try {
    const token = localStorage.getItem("token");
    const resp = await fetch(`${url}/updateUser/${id}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(user),
    });
    const json = await resp.json();
    return json;
  } catch (error) {
    console.log(error);
  }
};

export const DeleteUser = async (id) => {
  try {
    const token = localStorage.getItem("token");
    const resp = await fetch(`${url}/deleteUser/${id}`, {
      method: "DELETE",
      headers: {
        Authorization: `Bearer ${token}`,
      }
    });
    const json = await resp.json();
    return json;
  } catch (error) {
    console.log(error);
  }
};
