import { createSlice } from "@reduxjs/toolkit";
import { getAllAsync, getByName } from "./async-redux";

const initialState = {
  products: [],
};

const sortArray = (array, type) => {
  let toSort = [...array];
  if (type === "Alfabeticamente [A-Z]") {
    toSort.sort((a, b) => {
      let fa = a.name.toLowerCase(),
        fb = b.name.toLowerCase();

      if (fa < fb) {
        return -1;
      }
      if (fa > fb) {
        return 1;
      }
      return 0;
    });
  } else if (type === "Alfabeticamente [Z-A]") {
    toSort.sort((a, b) => {
      let fa = a.name.toLowerCase(),
        fb = b.name.toLowerCase();
      if (fa > fb) {
        return -1;
      }
      if (fa < fb) {
        return 1;
      }
      return 0;
    });
  } else if (type === "Precio mayor a menor") {
    toSort.sort((a, b) => {
      return b.price - a.price;
    });
  } else if (type === "Precio menor a mayor") {
    toSort.sort((a, b) => {
      return a.price - b.price;
    });
  }
  return toSort;
};

export const productSlice = createSlice({
  name: "products",
  initialState,
  reducers: {
    sortProducts: (state, action) => {
      const { type, array } = action.payload;
      state.products = sortArray(array, type);
    },
  },
  extraReducers: {
    [getAllAsync.fulfilled]: (state, action) => {
      state.products = action.payload.products;
    },
    [getAllAsync.rejected]: (state) => {
      state.products = [];
    },
    [getByName.fulfilled]: (state, action) => {
      if (action.payload.data) {
        state.products = action.payload.data;
      } else {
        state.products = [];
      }
    },
    [getByName.rejected]: (state, action) => {
      state.products = [];
    },
  },
});

export const { getAll, sortProducts } = productSlice.actions;

export default productSlice.reducer;
