import { createSlice } from "@reduxjs/toolkit";
import { addItemLS, cleanLS, editItemsLS } from "../components/helpers/localStorage";

const initialState = {
  shopCart: [],
};

export const shopCartSlice = createSlice({
  name: "shopCart",
  initialState,
  reducers: {
    addItem: (state, action) => {
      let itemFind = state.shopCart.find(
        (item) => item._id === action.payload._id
      );
      if (!itemFind) {
        state.shopCart.push(action.payload);
        addItemLS(action.payload);
      };
    },
    setItems: (state, action) => {
      state.shopCart = action.payload;
    },
    deleteOne: (state, action) => {
      const itemFind = state.shopCart.find((item) => item._id === action.payload);
      const toLS = state.shopCart.filter(item => item._id !== action.payload);
      if (itemFind) {
        state.shopCart.splice(state.shopCart.indexOf(itemFind), 1);
        editItemsLS(toLS);
      };
    },
    deleteAll: (state, action) => {
      state.shopCart = [];
      cleanLS();
    },
  },
});

export const { addItem, setItems, deleteAll, deleteOne } =
  shopCartSlice.actions;

export default shopCartSlice.reducer;